<template>
  <div class="row mt-4 align-items-start">
    <div class="col-md-4">
      <CartList />
    </div>
    <div class="col-md-8">
      <h2 class="h5 text-brown font-weight-normal mb-4 margin-mobile"></h2>
      <form @submit.prevent="createOrder(form)">
        <div class="row">
          <div class="form-group col-6">
            <label class="text-secondary" for="name"></label>
            <input class="form-control" v-model="form.user.name" type="text" id="name"
                    :class="{'is-invalid': errors.has('name')}"
                    v-validate="'required'" name="name">
            <span class="text-danger" v-if="errors.has('name')"></span>
          </div>
          <div class="form-group col-6">
            <label class="text-secondary" for="phone"></label>
            <input class="form-control" v-model="form.user.tel" type="number" id="phone"
                    :class="{'is-invalid': errors.has('phone')}"
                    v-validate="'required'" name="phone">
            <span class="text-danger" v-if="errors.has('phone')"></span>
          </div>
        </div>
        <div class="form-group">
          <label class="text-secondary mt-4" for="mail"></label>
          <input class="form-control" v-model="form.user.email" type="text" id="mail"
                  :class="{'is-invalid': errors.has('mail')}"
                  v-validate="'required|email'" name="mail">
          <span class="text-danger"
                v-if="errors.has('mail')">{{ errors.first('mail') }}
          </span>
        </div>
        <div class="form-group">
          <label class="text-secondary mt-4" for="address"></label>
          <input class="form-control" v-model="form.user.address" type="text" id="address"
                  :class="{'is-invalid': errors.has('address')}"
                  v-validate="'required'" name="address">
          <span class="text-danger" v-if="errors.has('address')"></span>
        </div>
        <div class="form-group">
          <label class="text-secondary mt-4" for="content">
            
            <small class="text-brown">()</small>
          </label>
          <textarea cols="50" rows="10" class="form-control"
                    v-model="form.message" id="content"
                    :class="{'is-invalid': errors.has('address')}"
                    v-validate="'required'" name="address">
          </textarea>
          <span class="text-danger" v-if="errors.has('address')"></span>
        </div>
        <hr>
        <button class="btn btn-block btn-brown" type="submit"></button>
      </form>
    </div>
  </div>
</template>

<script>
import CartList from '../components/CartList.vue';

export default {
  name: 'Cartform',
  data() {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  components: {
    CartList,
  },
  methods: {
    getCarts() {
      this.$store.dispatch('clientCart/clientGetCarts');
    },
    createOrder(form) {
      this.$validator.validate().then((result) => {
        if (result && this.$store.state.clientCart.carts.length > 0) {
          this.$store.dispatch('clientCart/clientCreateOrder', form);
        } else {
          this.$store.commit('ERROR', {
            error: true,
            message: '  !  ^^',
          });
          setInterval(() => {
            this.$store.commit('ERROR', {
              error: false,
              message: '',
            });
          }, 3000);
        }
      });
    },
  },
  created() {
    this.getCarts();
  },
};
</script>
